@charset "UTF-8";
/*------------------------------------*\
    Variables
\*------------------------------------*/
/*------------------------------------*\
    Global Box Sizing
\*------------------------------------*/
*,
*:after,
*:before {
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

.donation__box {
  font: 300 11px/1.4 "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #444;
}

.productWrapper a {
  font-size: 1rem;
}

/*------------------------------------*\
    Clear
\*------------------------------------*/
.clear:before,
.clear:after {
  content: " ";
  display: table;
}

.clear:after {
  clear: both;
}

/*------------------------------------*\
    Images
\*------------------------------------*/
img {
  max-width: 100%;
  vertical-align: bottom;
}

/*------------------------------------*\
    MISC
\*------------------------------------*/
::selection {
  background: #04A4CC;
  color: #fff;
  text-shadow: none;
}

::-webkit-selection {
  background: #04A4CC;
  color: #fff;
  text-shadow: none;
}

::-moz-selection {
  background: #04A4CC;
  color: #fff;
  text-shadow: none;
}

/*------------------------------------*\
    WORDPRESS CORE
\*------------------------------------*/
.alignnone {
  margin: 5px 20px 20px 0;
}

.aligncenter,
div.aligncenter {
  display: block;
  margin: 5px auto 5px auto;
}

.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

a img.alignnone {
  margin: 5px 20px 20px 0;
}

a img.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption {
  background: #fff;
  border: 1px solid #F0F0F0;
  max-width: 96%;
  padding: 5px 3px 10px;
  text-align: center;
}

.wp-caption.alignnone {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignleft {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignright {
  margin: 5px 0 20px 20px;
}

.wp-caption img {
  border: 0 none;
  height: auto;
  margin: 0;
  max-width: 98.5%;
  padding: 0;
  width: auto;
}

.wp-caption .wp-caption-text,
.gallery-caption {
  font-size: 11px;
  line-height: 17px;
  margin: 0;
  padding: 0 4px 5px;
}

/*------------------------------------*\
    Input
\*------------------------------------*/
input:focus {
  outline: 0;
  border: 1px solid #04A4CC;
}

/*------------------------------------*\
    Gravity
\*------------------------------------*/
/* Gravity Forms Calendar CSS */
.gform_wrapper {
  text-align: left !important;
  font-family: Calibri, "Open Sans", sans-serif !important;
  line-height: normal !important;
}

#gform_submit_button_6,
#gform_submit_button_7 {
  background-color: #04A4CC;
  color: white !important;
  text-transform: capitalize;
  font-weight: normal;
  border-color: #04A4CC;
  border-radius: 50px;
  box-shadow: 0 !important;
  border: 0;
  min-width: 250px;
}

body img.gform_ajax_spinner {
  max-width: 50px;
  margin: 0 auto;
}

.gform_wrapper.gravity-theme .gform_validation_errors > h2 .gform-icon {
  position: relative;
}

.gform_wrapper.gravity-theme .gform_validation_errors > h2 {
  font-size: 13.2px !important;
}

.gutenberg__wrap input::-ms-clear {
  display: none;
}

/*------------------------------------*\
    Anchor
\*------------------------------------*/
a {
  color: #444;
  text-decoration: none;
}

a:hover {
  color: #444;
}

a:focus {
  outline: 0;
}

a:hover,
a:active {
  outline: 0;
}

.gutenberg__wrap ol,
.gutenberg__wrap ul {
  list-style: none;
}

.gutenberg__wrap blockquote,
.gutenberg__wrap q {
  quotes: none;
}

.gutenberg__wrap blockquote:after,
.gutenberg__wrap blockquote:before,
.gutenberg__wrap q:after,
.gutenberg__wrap q:before {
  content: "";
  content: none;
}

.gutenberg__wrap table {
  border-collapse: collapse;
  border-spacing: 0;
}

.gutenberg__wrap h1.center {
  background: url("assets/img/h1-bg.png") bottom center no-repeat;
  background-size: 260px;
}

