  font-style: normal;
  font-weight: 900;
  src: url(assets/fonts/fontawesome/webfonts/fa-solid-900.eot);
  src: url(assets/fonts/fontawesome/webfonts/fa-solid-900.eot?#iefix) format("embedded-opentype"), url(assets/fonts/fontawesome/webfonts/fa-solid-900.woff2) format("woff2"), url(assets/fonts/fontawesome/webfonts/fa-solid-900.woff) format("woff"), url(assets/fonts/fontawesome/webfonts/fa-solid-900.ttf) format("truetype"), url(assets/fonts/fontawesome/webfonts/fa-solid-900.svg#fontawesome) format("svg");
}
.fa, .fas {
  font-family: Font Awesome\ 5 Pro;
  font-weight: 900;
}

.fab {
  font-family: "Font Awesome 5 Brands" !important;
}

.spinner {
  -webkit-animation-name: spin;
  -webkit-animation-duration: 4000ms;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
  -moz-animation-name: spin;
  -moz-animation-duration: 4000ms;
  -moz-animation-iteration-count: infinite;
  -moz-animation-timing-function: linear;
  -ms-animation-name: spin;
  -ms-animation-duration: 4000ms;
  -ms-animation-iteration-count: infinite;
  -ms-animation-timing-function: linear;
  animation-name: spin;
  animation-duration: 4000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@-ms-keyframes spin {
  0% {
    -ms-transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
  }
}
@-moz-keyframes spin {
  0% {
    -moz-transform: rotate(0deg);
  }
  to {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  11.11% {
    opacity: 1;
  }
  33.33% {
    opacity: 1;
  }
  44.44% {
    opacity: 0;
  }
  to {
    opacity: 0;
  }
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  11.11% {
    opacity: 1;
  }
  33.33% {
    opacity: 1;
  }
  44.44% {
    opacity: 0;
  }
  to {
    opacity: 0;
  }
}
@-webkit-keyframes ripple-in1 {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  20% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0.3;
  }
  to {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0;
  }
}
@keyframes ripple-in1 {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  20% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0.3;
  }
  to {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0;
  }
}
@-webkit-keyframes ripple-in2 {
  0% {
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
  }
  20% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    opacity: 0.3;
  }
  to {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    opacity: 0;
  }
}
@keyframes ripple-in2 {
  0% {
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
  }
  20% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    opacity: 0.3;
  }
  to {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    opacity: 0;
  }
}
